import React, { useRef, useState } from 'react';
import emailjs from '@emailjs/browser';
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";


/* Schema */

const validationSchema = Yup.object().shape({
    name: Yup.string()
        .required("İsim zorunludur.")
        .min(3, "İsim en az 3 karakter uzunluğunda olmalıdır."),
    email: Yup.string()
        .email("Geçerli bir e-posta adresi girin.")
        .required("E-posta zorunludur."),
    message: Yup.string()
        .required("Mesaj zorunludur.")
        .min(10, "Mesaj en az 10 karakter uzunluğunda olmalıdır."),
});

const initialValues = {
    name: "",
    email: "",
    message: "",
};




function Contact() {

    const [btnCtx, setBtnCtx] = useState(0);

    const form = useRef();

    const onSubmit = (e) => {
        e.preventDefault();


        emailjs.sendForm('service_8ajg4l4', 'template_rlkv1sq', form.current, 'oQT0rtKXghENul39r')
            .then((result) => {
                setBtnCtx(1)
            }, (error) => {
                alert("Mesajın gönderilirken bir hata oluştu. \n internetin kesilmiş veya teknik bir sorun olabilir.")
            });


    };



    return (
        <section id='section' className='mx-8 my-24 gap-4 flex flex-col justify-center items-center'>
            <div className='SkillBox flex flex-col gap-4 items-center justify-center'>
                <h1 className='text-center title font-bold text-4xl'>
                    İLETİŞİM
                </h1>
            </div>
            <div>
                <Formik
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                >
                    {({ errors, touched }) => (
                        <Form ref={form} onSubmit={onSubmit}>
                            <div>
                                <label htmlFor="name" className='label'>İsim</label>
                                <Field type="text" className="field" name="name" id="name" />
                                <ErrorMessage name="name" component="div" className="error" />
                            </div>


                            <div className='my-4'>
                                <label htmlFor="email" className='label'>E-posta</label>
                                <Field type="email" className="field" name="email" id="email" />
                                <ErrorMessage name="email" component="div" className="error" />
                            </div>

                            <div>
                                <label htmlFor="message" className='label'>Mesaj</label>
                                <Field
                                    className="field"
                                    as="textarea"
                                    name="message"
                                    id="message"
                                    rows="5"
                                    cols="30"
                                    style={{ height: 190 }}
                                />
                                <ErrorMessage name="message" component="div" className="error" />
                            </div>
                            <button type="submit" className='sendBtn'>
                                {btnCtx ? "Gönderildi" : "Gönder"}
                            </button>


                        </Form>
                    )}
                </Formik>
            </div>
        </section>
    )
}

export default Contact